<!-- 编辑店铺 -->

<template>
    <div class="wrap">
        <el-upload class="avatar-uploader" action="https://jsonplaceholder.typicode.com/posts/" :show-file-list="false"
            :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <img :src="require('@/assets/img' + msg.pic)" alt="" width="100" height="100">
            <div class="span">修改</div>
        </el-upload>
        <div class="sec1">
            <span>店铺名称</span>
            <div class="inp">
                <el-input v-model="input1" placeholder="请输入店铺名称" style="width:100%;"></el-input>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ['msg'],
    data() {
        return {
            imageUrl: '',
            input1: this.msg.name,
        }
    },
    watch: {
        "msg.name"(a, b) {
            this.input1 = a
        }
    },
    methods: {
        // 上传头像
        handleAvatarSuccess(res, file) {
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG) {
                this.$message.error('上传头像图片只能是 JPG 格式!');
            }
            if (!isLt2M) {
                this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
        }
    }
}
</script>

<style lang="scss" scoped>
// 上传头像
.avatar-uploader {
    border-radius: 10px;
    background-color: #ddd;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    width: 100px;
    margin-bottom: 25px;

    .el-upload:hover {
        border-color: #409EFF;
    }

    .avatar {
        width: 80px;
        height: 80px;
        display: block;
    }

    .span {
        position: absolute;
        background-color: rgba($color: #a9a9a9, $alpha: 0.9);
        bottom: 0;
        padding: 3px 0;
        box-sizing: border-box;
        width: 100%;
        z-index: 10;
        color: #fff;
        text-align: center;
        font-size: 12px;

    }
}

.wrap {
    padding: 0 !important;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    .sec1 {
        width: 100%;
        display: flex;
        margin-bottom: 20px;
        align-items: center;

        .inp {
            width: calc(100% - 100px);
        }

        span {
            width: 80px;
            background-color: 100px;
        }
    }

    .sec1:nth-of-type(1) {
        align-items: flex-start;
    }
}
</style>
