<template>
    <div class="wrap">
        <div class="search1">
            <div class="left">
            </div>
            <div class="right">
                <el-input placeholder="搜索推送标题" v-model="search1" style="width:300px; margin-right:10px;">
                    <i slot="prefix" class="el-input__icon el-icon-search"></i>
                </el-input>
                <el-button type="primary" @click="searchs1">查询</el-button>
                <el-button type="primary" @click="dialogVisible = true">发布推送</el-button>
            </div>
        </div>
        <div class="table">
            <el-table :data="filterData" style="width: 100%" highlight-current-row="true">
                <el-table-column type="selection" width="100">
                </el-table-column>
                <el-table-column width="200px" align="center" label="推送标题">
                    <template slot-scope="scope">
                        <div class="tb">
                            <div class="tu">
                                <img :src='require("../../../assets/img" + scope.row.pic)'
                                    style="width:100%;height:100%;">
                            </div>
                            {{ scope.row.name }}
                        </div>
                    </template>
                </el-table-column>
                <el-table-column prop="time" label="活动时间" align="center">
                </el-table-column>
                <el-table-column label="操作" align="center" width="200">
                    <template slot-scope="scope">
                        <div class="operate">
                            <el-button type="text" @click="editshop(scope.row)">编辑</el-button>
                            <el-button type="text" @click="dialogVisible2 = true">删除</el-button>
                            <el-button type="text">下架</el-button>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
            <div class="footer">
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                    :current-page.sync="currentPage1" :page-size="4" layout="total, prev, pager, next"
                    :total="this.tableData.length">
                </el-pagination>
            </div>
        </div>
        <!-- 发布推送 -->
        <el-dialog title="发布推送" :visible.sync="dialogVisible" width="40%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kuang">
                <PushAdg />
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 编辑店铺 -->
        <el-dialog title="编辑推送" :visible.sync="dialogVisible1" width="30%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kuang">
                <Editshop :msg="propsmsg" />
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible1 = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible1 = false">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 删除提示 -->
        <el-dialog title="提示" :visible.sync="dialogVisible2" width="25%" lock-scroll=false :before-close="handleClose"
            align="left">
            <div class="kuang">
                <Deletetag />
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible2 = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible2 = false">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { platformPush } from '@/request/api'
import PushAdg from './components/PushAdg'
import Deletetag from '@/components/deletetag'
import Editshop from '../CouponGroupPurchase/components/editshop'
export default {
    beforeCreate() {
        platformPush().then(res => {
            this.tableData = res
            this.filterData = res.filter((e, i) => {
                return i < 4
            })
        })
    },
    data() {
        return {
            search1: null,
            tableData: null,
            filterData: null,
            filterData1: null,
            propsmsg: null,
            currentPage1: 1,
            dialogVisible: false,
            dialogVisible1: false,
            dialogVisible2: false
        }
    },
    watch: {

    },
    methods: {
        // 分页
        handleCurrentChange(val) {
            this.currentPage1 = val
            this.filterData = this.tableData.filter((e, i) => {
                return i >= (val - 1) * 4 && i <= (val * 4 - 1)
            })
        },
        //查询
        searchs1() {
            if (this.search1 == "") {
                this.filterData = this.filterData1
            }
            else {
                this.filterData1 = this.filterData
                this.filterData = this.tableData.filter((e, i) => {
                    return e.name == this.search1
                })
            }
        },
        // 编辑
        editshop(val) {
            this.dialogVisible1 = true
            this.propsmsg = val
        }
    },
    components: {
        PushAdg, Deletetag, Editshop
    }
}
</script>
<style lang="scss" scoped>
* {
    box-sizing: border-box;
}

.wrap {
    width: 100%;
    height: 100%;
    padding: 20px;

    .search1 {
        height: 100px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left {
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            .h1 {
                font-size: 18px;
                font-weight: 700;
                margin-bottom: 20px;
            }

            .cate {
                display: flex;
                font-size: 14px;
                color: #c5c5c5;

                .cat1 {
                    margin-right: 30px;
                }
            }
        }

        .right {
            display: flex;
            align-items: center;
        }
    }

    .table {
        .tb {
            width: 100%;
            display: flex;
            align-items: center;

            // background-color: red;
            .tu {
                width: 50px;
                height: 50px;
                border-radius: 10px;
                margin-right: 10px;

                img {
                    width: 100%;
                    height: 100%;
                    border-radius: 10px;

                }
            }
        }
    }

    .footer {
        margin-top: 50px;
    }


    .kung {
        width: 100%;
        height: 500px;
        overflow: auto;
        padding: 0;
    }

    .kuang {
        width: 100%;
        padding: 0;
    }


}
</style>
